import { CommonAPI } from "./CommonAPI";
import { getAccounts, getBalance } from "../helpers/web3Helpers";
import { getTokenBalance, bet } from "../contractHelps/tokenContract";
import { getWeb3 } from "../helpers/getWeb3";

export class CommonAPIReal implements CommonAPI {

    public async getWeb3(): Promise<any> {
        return getWeb3();
    }

    async getTTBalance(): Promise<number> {
        const web3 = await getWeb3();
        const tb = await getBalance();
        const ttBalance = web3.fromWei(tb.toNumber(), 'ether');
        return ttBalance;
    }

    async getTokenBalance(): Promise<number> {
        const web3 = await getWeb3();
        const tb = await getTokenBalance();
        const tokenBalance = web3.fromWei(tb.toNumber(), 'ether');
        return tokenBalance;
    }

    public async getAccount(): Promise<string[]> {
        return getAccounts();
    }

    public async submitGame(params: any): Promise<void> {
        console.log(params);
        const web3 = await getWeb3();
        const tokenBalance = await getTokenBalance();
        if (tokenBalance.lt(params.money)) {
            throw new Error('余额不足');
        }
        return bet(params.betAddress, params.betType, web3.toWei(params.money, 'ether'), params.issueNo, params.nums, params.amounts);
    }

}

export default new CommonAPIReal();
